//color de fondo
.nat-bgc--white {
    background: $white;
}
.nat-bgc--gray {
    background: $gray-white;
}
.nat-bgc--cian {
    background: $color_cian;
    h1,h2,h3,h4,h5,h6,p,span,strong,b,small,a,em,mark,i,u,s,cite,sub,sup,q,dfn,abbr,time,legend {
        color: $white;
    }
    .swiper-pagination-bullet{
        background: $white;
        opacity: 1;
    }
    .swiper-pagination-bullet-active{
        background: $blue-dark;
    }
}
.nat-bgc--blue {
    background: $skyblue;
    h1,h2,h3,h4,h5,h6,p,span,strong,b,small,a,em,mark,i,u,s,cite,sub,sup,q,dfn,abbr,time,legend {
        color: $white;
    }
}
.nat-bgc--blue-dark {
    background: $blue-dark;
    h1,h2,h3,h4,h5,h6,p,span,strong,b,small,a,em,mark,i,u,s,cite,sub,sup,q,dfn,abbr,time,legend {
        color: $white;
    }
}
.nat-bgc--azul {
    background-color: $color_azul-dark;
    h1,h2,h3,h4,h5,h6,p,span,strong,b,small,a,em,mark,i,u,s,cite,sub,sup,q,dfn,abbr,time,legend {
        color: $white;
    }
}
.nat-bgc--gris {
    background: #f4f4f3;
}
.nat-bgc--verde {
    background: $color_verde;
    h1,h2,h3,h4,h5,h6,p,span,strong,b,small,a,em,mark,i,u,s,cite,sub,sup,q,dfn,abbr,time,legend {
