//color de fondo banner c2c
.nat-banner-c2c--white {
    background: $white;
    color: $gray-dark2;
}
.nat-banner-c2c--gray{
    background: $gray-white;
    color: $gray-dark2;
}
.nat-banner-c2c--blue{
    background: $skyblue;
    color: $white;
}
.nat-banner-c2c--blue-dark{
    background: $blue-dark;
    color: $white;
}

// Color de texto
.txt-color-gray {
