.modal{
    width: 100%;
    max-width: 850px;
    border-radius: 5px;
    max-height: 85%;
    @media (min-height: 767px){
        max-width: 1110px;
    }
    &:focus{
        outline: none;
    }
    .modal-header{
        text-align: right;
        padding: 12px 0;
        @media only screen and (min-width:992px){
            padding: 8px 0;
        }
        .modal-close{
            font-size: 28px;
            @media only screen and (min-width:992px){
                font-size: 22px;
            }
            .icon{
                line-height: 1.5
            }
            &:focus{
                outline: none;
            }
        }
    }
    .modal-content{
        padding: 0 23px 32px;
        @media only screen and (min-width:992px){
            padding: 0 15px 25px;
        }
        .responsive-video{
            width: 100%;
            display: block;
            border-radius: 5px;
        }
    }
}