.tabs__list{
    text-align: center;
    display: block;
    margin-top: 30px;
    margin-bottom: 0;
    padding: 0 5px 0 15px;
    height: auto;
    @media only screen and (min-width: 992px){
        margin-top: 60px;
        padding: 0;
    }
    .indicator{
        height: 40px;
        background: $color-cian;
        border-radius: 100px;
        bottom: inherit;
        top: 0;
    }
}
.tab__item{
    height: 40px;
    text-transform: initial;
    padding: 0 10px;
    .item{
        @include tipography-300;
        font-size: 16px;
        line-height: 39px;
        text-align: center;
        color: $gray;            
        text-overflow: inherit;
        width: 100%;
        min-width: 100px;
        padding: 0 20px;
        position: relative;
        z-index: 1;
        &:focus{
            background: transparent !important;
        }
        &:hover{
            color: $gray;
        }
    }
    .item.active{
        color: #fff;
        background: transparent !important;
        
    }
}
.tabs__content{
    width: 100%;
    position: relative;
}