.natListItem{
    padding: 30px 0;
    @media only screen and (min-width: 992px){
        padding: 60px 0;
    }
    .container{
        width: 100%;
        max-width: 600px;
        @media only screen and (min-width: 992px){
            max-width: 1366px;
        }
    }
    .natListItem__header{
        margin-bottom: 100px;
        @media only screen and (min-width: 992px){
            margin-bottom: 50px;
        }
        .title{
            @include tipography-300;
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            margin:0;
            @media only screen and (min-width: 992px){
                font-size: 40px;
                line-height: 50px;
            }
        }
    }
    .natListItem__content{
        .list{
            margin-top: 0;
            margin-bottom: 0;
            @media only screen and (min-width: 992px){                
                display: flex;
                flex-wrap: nowrap;
                justify-content: center;
            }       
        }
        .item{
            padding: 0 15px;
            margin-bottom: 50px;
            &:last-of-type{
                margin-bottom: 0;
            }
            @media only screen and (min-width: 992px){
                padding: 0 35px;
                margin-bottom: 0;
                margin-left: 0;
            }
        }
        .item.col.l4{
            padding: 0 15px;
            @media only screen and (min-width: 992px){
                padding: 0 60px;
                margin-bottom: 0;                
            }
        }
        .item__head{
            text-align: center;
            margin-bottom: 18px;
            img{
                @media only screen and (min-width: 992px){
                    height: 118px;
                }
            }
        }
        .item__content{
            .title{
                @include tipography-bold;
                font-size: 20px;
                line-height: 24px;
                text-align: center;
            }
            .text{
                @include tipography-normal;
                font-size: 16px;
                line-height: 24px;
                text-align: center;
            }
        }
    }
}
.natListItem.natListItem--grid{
    .natListItem__header{
        margin-bottom: 40px;
        @media only screen and (min-width: 992px){
            margin-bottom: 50px;
        }
    }
    .natListItem__content{
        .list{
            @media only screen and (min-width: 992px){
                flex-wrap: wrap;
                align-items: baseline;
            }
        }
        
        .item{
            margin-bottom: 0;
            @media only screen and (min-width: 992px){
                margin-bottom: 25px !important;
            }
            &:nth-last-child(-n+3){
                margin-bottom: 0 !important;
            }
        }
        .swiper-pagination{
            position: relative;
            margin-top: 10px;
            bottom: 0;
            @media only screen and (min-width: 992px){
                display: none;
            }
            .swiper-pagination-bullet{
                background: $white;
                opacity: 1;
            }
            .swiper-pagination-bullet-active{
                background: $blue-dark;
            }
        }
    }
}

.natListItem.natListItem--grid.nat-bgc--white,
.natListItem.natListItem--grid.nat-bgc--gray{
    .swiper-pagination{
        .swiper-pagination-bullet{
            background: $gray-light;
            opacity: 1;
        }
        .swiper-pagination-bullet-active{
            background: $skyblue;
        }
    }
}

.natListItem.natListItem--horizontal{
    .container{
        max-width: 600px;
        @media only screen and (min-width: 992px){
            max-width: 1045px;
        }
    }
    .natListItem__header{
        margin-bottom: 60px;
    }
    .natListItem__content{
        .list{
            display: block
        }
        .item{
            padding: 0;
            margin-bottom: 60px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    .item__content{        
        margin-bottom: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        @media only screen and (min-width: 992px){
            display: block;
        }
        .title{
            text-align: left;
        }
        .text{
            text-align:left;
        }
        @media only screen and (min-width: 992px){
            .push-l7{
                left: 66.333333%;
            }
        }
    }
    .item__picture{
        text-align: center;
        margin-bottom: 45px;
        @media only screen and (min-width: 992px){
            margin-bottom: 0;
        }
        .picture{
            width: 100%;
            max-width: 285px;
        }
    }
}