.footer {
    &.footer--nav {
        position: relative;
        padding: 32px 0;
        @media only screen and (min-width:992px){
            padding: 32px 0 83px;
        }    
        .container{
            width: 100%;
            max-width: 1280px;
            padding-left: 1rem;
            padding-right: 1rem;
        }
        .footer__brand{
            padding-bottom: 32px;
            border-bottom: 1px solid #F4F4F3;
            @media only screen and (min-width:992px){
                border-bottom: 4px solid #F4F4F3;
            }
            .footer__logo{
                width: 218px;
                @media only screen and (min-width:992px){
                    width: auto;
                }
            }
        }
        .footer__nav{
            .container{
                padding: 0;
            }
            @media only screen and (min-width:992px){
                padding: 44px 0 158px;
            }
            .collapsible{
                list-style: none;
                border: 0;
                margin: 0;
                padding: 0;
                box-shadow: inherit;
                @media only screen and (min-width:992px){
                    display: flex;
                    justify-content: space-between;
                    align-items: baseline;
                }
                .collapsible--item{
                    @media only screen and (min-width:992px){
                        padding: 0 15px;
                    }
                    .collapsible-header{
                        padding: 25px 15px;
                        border-bottom: 1px solid #F4F4F3;
                        position: relative;
                        @media only screen and (min-width:992px){
                            border: 0;
                            padding: 0;
                            margin-bottom: 33px;
                            pointer-events: none;
                            cursor: initial;
                        }
                        .title{
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 20px;
                            color: $gray-dark;
                            @media only screen and (min-width:992px){
                                font-weight: bold;
                                font-size: 18px;
                                line-height: 24px;
                            }
                        }
                        .image{
                            position: absolute;
                            right: 15px;
                            top: 50%;
                            transform: translate(0, -50%) rotate(0);
                            @media only screen and (min-width:992px){
                                display: none;
                            }
                        }
                        &:focus{
                            background: #fff;
                            outline: none;
                        }
                    }
                    .collapsible-body{
                        padding: 0;
                        border: 0;
                        @media only screen and (min-width:992px){
                            display: block !important;
                        }
                        .nav{
                            list-style: none;
                            margin: 0;
                            padding: 0;
                            border-bottom: 1px solid #F4F4F3;
                            @media only screen and (min-width:992px){
                                margin: -10px 0;
                                border-bottom: none;
                            }
                            .nav__item{
                                padding: 15px;
                                @media only screen and (min-width:992px){
                                    padding: 10px 0;
                                }
                            }
                            .nav__link{
                                font-weight: normal;
                                font-size: 16px;
                                line-height: 24px;
                                color: $gray;
                                display: block;
                                transition: color 0.25s ease-out;
                                &:hover, &:active {
                                    color: $gray-dark;
                                    background-color: transparent;
                                    transition: color 0.25s ease-in;
                                }
                                &:focus {
                                    color: $gray-dark;
                                    background-color: transparent;
                                }
                            }
                        }
                    }
                    &.active{
                        .collapsible-header{
                            .image{
                                transform: translate(0, -50%) rotate(180deg);
                            }
                        }
                    }
                }
            }
        }
        .footer__claims{
            .container{
                max-width: 1230px;
                @media only screen and (min-width:992px){
                    display: flex;
                    justify-content: space-between;
                }
            }
            .nav{
                margin: 0;            
            }
            .nav--claims{
                display: flex;
                flex-wrap: wrap;
                padding-top: 15px;
                align-items: baseline;
                @media only screen and (min-width:992px){
                    flex-wrap: inherit;
                    padding-top: 0;
                }
                .nav__item{
                    padding: 10px 0;
                    width: 100%;
                    @media only screen and (min-width:992px){
                        padding: 0 21px;
                        width: auto;
                    }
                    &:nth-child(1){
                        .nav__link{
                            font-weight: bold;
                            img{
                                position: relative;
                                bottom: 3px;
                                height: 26px;
                                margin-right: 8px;
                                @media only screen and (min-width:992px){
                                    height: 45px;
                                    margin-right: 9px;
                                }
                            }
                        }
                    }
                }
                .nav__link{
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 20px;
                    color: $gray;
                    display: block;
                    transition: color 0.25s ease-out;
                    &:hover, &:active {
                        color: $gray-dark;
                        background-color: transparent;
                        transition: color 0.25s ease-in;
                    }
                    &:focus {
                        color: $gray-dark;
                        background-color: transparent;
                    }
                }
            }
            .nav--rrss{
                display: flex;
                padding-top: 15px;
                @media only screen and (min-width:992px){
                    padding-top: 0;
                }
                .nav__item{
                    padding: 0 5px;
                    text-align: center;
                    
                }
                .nav__link{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 28px;
                    height: 28px;
                    font-size: 10px;
                    font-weight: 300;
                    color: #fff;
                    text-decoration: none;
                    background-color: $gray-dark;
                    border-radius: 50%;
                    .icon{
                        transform: rotate(0);
                        fill: currentColor;
                        transition: all ease .5s;
                    }
                    @media only screen and (min-width:992px){
                        &:hover, &:active {
                            .icon{                                
                                transform: rotate(360deg);
                            }
                        }
                    }
                }
            }
        }
    }
}